.feuerwehr__possibility {
    display: flex;
    flex-direction: row;
}

.feuerwehr__possibility-image {
    flex: 1;
    display: grid;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem ;
}

.feuerwehr__possibility-image img {
    width: 100%;
    height: 100%;
    border: 5px solid rgb(203, 149, 43);
    border-radius: 10px;
}

.feuerwehr__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.feuerwehr__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.feuerwehr__possibility-content h4:last-child {
    color: #FF8A71;
}

.feuerwehr__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.feuerwehr__possibility-content h2 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
    color: white;
}

.feuerwehr__possibility-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}

@media screen and (max-width: 950px) {
    .feuerwehr__possibility {
        flex-direction: column;
    }

    .feuerwehr__possibility-image {
        margin: 1rem 0;
    }

    .feuerwehr__possibility-content {
        margin-top: 2rem;
    }
}