.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.715);

    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-inner {
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: #FFF;
    font-family: var(--font-family);
}

.popup-inner p {
    color: black;
}


.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}

.feuerwehr__cookie-btn button {
    background-color: rgba(20,228,124,255);
    color: white;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 14px;
    line-height: 25px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
    margin-top: 35px;
}

@media screen and (max-width: 650px) {
        .feuerwehr__popup-btn {
        margin-top: 0px;
    }
}