.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.715);

    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-inner {
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: #FFF;
    font-family: var(--font-family);
}

.popup-inner p {
    color: black;
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}

.feuerwehr__popup-btn button {
    background: black;
    color: white;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 14px;
    line-height: 25px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 160px;
    margin-top: 50px;
}

.feuerwehr__popup-btn button:hover {
    background: var(--gradient-hover);
}

.feuerwehr__popup-btn img {
    cursor: pointer;
}

.feuerwehr__popup-img img {
    width: 100%;
    cursor: none;
}

@media screen and (max-width: 650px) {
        .feuerwehr__popup-btn {
        margin-top: 0px;
    }
}