.feuerwehr__about {
    display: flex;
    flex-direction: row;
}

.feuerwehr__about-image {
    flex: 1;
    display: grid;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem ;
}

.feuerwehr__about-image img {
    border-radius: 5px;
}

.feuerwehr__about-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.feuerwehr__about-content p {
    cursor: pointer;
}

.feuerwehr__kommandant p:hover {
    color: #71E5FF;
}

.feuerwehr__about-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}

.feuerwehr__about-content h4:last-child {
    color: #FF8A71;
}

.feuerwehr__about-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 1rem 0;
}

.feuerwehr__about-content h2 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25px;
    line-height: 34px;
    margin: 1rem 0;
    color: white;
}

.feuerwehr__about-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}



@media screen and (max-width: 950px) {
    .feuerwehr__about {
        flex-direction: column;
    }

    .feuerwehr__about-image {
        margin: 1rem 0;
    }

    .feuerwehr__about-content {
        margin-top: 2rem;
    }
}