.feuerwehr__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.feuerwehr__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.feuerwehr__cookie-btn h3 {
    margin-bottom: 15px;
}

.feuerwehr__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}

.feuerwehr__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}

.feuerwehr__footer-btn p {
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    word-spacing: 2px;
}

.feuerwehr__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.feuerwehr__footer-links div {
    width: 250px;
    margin: 1rem;
}

.feuerwehr__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.feuerwehr__footer-links_logo img {
    width: 118px;
    margin-bottom: 2rem;
}

.feuerwehr__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}

.feuerwehr__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

}

.feuerwehr__footer-links_div h4 {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #fff;
    margin-bottom: 1rem;
}

.feuerwehr__footer-links_div a {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    margin: 0.5rem 0;
    cursor: pointer;
}

.feuerwehr__footer-links_div p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
    margin: 0.5rem 0;
    cursor: pointer;
}

.feuerwehr__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.feuerwehr__footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .feuerwehr__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .feuerwehr__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .feuerwehr__footer-links_div {
        margin: 1rem 0;
    }

    .feuerwehr__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 550px) {
    .feuerwehr__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}

/*
default style for popup

*/
.popup-content {
    margin: auto;
    background-color: white;
    width: 100%;
    padding: 5px;
    border: 1px solid #d7d7d7;
  }
  .popup-arrow {
    color: rgb(255, 255, 255);
  }
  [role='tooltip'].popup-content {
    width: 500px;
    box-shadow: rgb(255, 255, 255) 0px 0px 3px;
    border-radius: 5px;
  }
  
  .popup-overlay {
    background: rgba(0, 0, 0, 0.5);
  }
  [data-popup='tooltip'].popup-overlay {
    background: transparent;
  }
  
  .popup-arrow {
    filter: drop-shadow(0 -3px 3px rgba(0, 0, 0, 0.16));
    color: #fff;
    stroke-width: 2px;
    stroke: #d7d7d7;
    stroke-dasharray: 30px;
    stroke-dashoffset: -54px;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  .cookie_link {
    font-size: 18px;
    font-family: var(--font-family);
  }

  .cookie_link:hover {
    color: rgb(20, 101, 20);
  }