@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(69deg, rgba(255,0,0,1) 0%, rgba(203,149,43,1) 100%);
  --gradient-bar: linear-gradient(69deg, rgba(255,0,0,1) 0%, rgba(203,149,43,1) 100%);
  --gradient-headline: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
  --gradient-hover: linear-gradient(90deg, rgba(19,178,212,1) 0%, rgba(77,100,199,1) 38%, rgba(227,20,20,1) 89%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}